/* 新导航栏 */
.fixed[data-v-7ff0292c] {
    width: 100%;
    left: 0;
    position: fixed;
    top: auto;
    bottom: 0;
    z-index: 99;
     line-height: 1.5;
}
.sub-nav.nav-b5[data-v-7ff0292c] {
    /* height: 48px; */
    overflow: hidden;
    margin: 0;
    padding: 0;
    background: #fff;
    border-top: 1px solid #ddd;
    /* box-shadow: 0 -3px 3px rgba(0, 0, 0, .1); */
    margin: 0;
}
.sub-nav.nav-b5 dd[data-v-7ff0292c] {
    margin: 0 0 0 0;
    text-align: center;
    border-right: 0px solid #ccc;
    -webkit-box-sizing: border-box;
    -o-box-sizing: border-box;
    box-sizing: border-box;
     width: 25%;
    font-size: 14px;
     float: left;
    display: inline;
    font-family: Helvetica, STHeiti STXihei, Microsoft JhengHei,Microsoft YaHei, Arial;
    font-weight: normal;
    color: #999999;
}
.sub-nav .nav-b5-relative[data-v-7ff0292c] {
    position: relative;
    padding: 2px 0;
}
.nav-b5-relative span[data-v-7ff0292c] {
    font-size: 13px;
}
.sub-nav a[data-v-7ff0292c] {
      text-decoration: none;
    color: #333;
    font-family: "微软雅黑", "Helvetica Neue", "Helvetica", Helvetica, Arial,
 sans-serif;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    outline: none;
}
.nav-b5-relative img[data-v-7ff0292c] {
    width: 28px;
    height: 28px;
    text-align: center;
    margin: 2px auto;
    display: block;
    border: 0 none;
    vertical-align: top;
}




